}
body {
  font-size: 16px;
}
.SiteHeader {
  background-color: #f55247;
  padding: 20px 0;
}
.SiteHeader__Inner {
  *zoom: 1;
  max-width: 1180px;
  margin-left: auto;
  margin-right: auto;
}
.SiteHeader__Inner:before{
    content: '';
    display: table;
}
.SiteHeader__Inner:after{
    content: '';
    display: table;
    clear: both;
}
.SiteHeader__Inner__SiteTitle {
  width: calc(99.99% * 3/12 - (30px - 30px * 3/12));
  margin: 0;
  color: #fdfdfd;
}
.SiteHeader__Inner__SiteTitle:nth-child(n){
    float: left;
    margin-right: 30px;
    clear: none;
}
.SiteHeader__Inner__SiteTitle:last-child{
    margin-right: 0;
}
.SiteHeader__Inner__SiteTitle:nth-child(12n){
    margin-right: 0;
}
.SiteHeader__Inner__SiteTitle:nth-child(12n + 1){
    clear: left;
}
.SiteHeader__Inner__Nav {
  width: calc(99.99% * 9/12 - (30px - 30px * 9/12));
  position: relative;
}
.SiteHeader__Inner__Nav:nth-child(n){
    float: left;
    margin-right: 30px;
    clear: none;
}
.SiteHeader__Inner__Nav:last-child{
    margin-right: 0;
}
.SiteHeader__Inner__Nav:nth-child(12n){
    margin-right: 0;
}
.SiteHeader__Inner__Nav:nth-child(12n + 1){
    clear: left;
}
.SiteHeader__Inner__Nav > *{
    position: absolute;
    top: 50%;
    right: 0;
    bottom: auto;
    left: auto;
    transform: translate(0, -50%);
}
.SiteHeader__Inner__Nav__Links__Link {
  display: inline-block;
  padding: 0 1rem 0 1rem;
}
.SiteHeader__Inner__Nav__Links__Link > a {
  color: #fff;
  text-decoration: none;
}
.SiteHeader__Inner__Nav__Links__Link > a:hover {
  text-decoration: underline;
}
.Forums {
  margin-top: 2rem;
  *zoom: 1;
  max-width: 1180px;
  margin-left: auto;
  margin-right: auto;
  *zoom: 1;
}
.Forums:before{
    content: '';
    display: table;
}
.Forums:after{
    content: '';
    display: table;
    clear: both;
}
.Forums:before{
    content: '';
    display: table;
}
.Forums:after{
    content: '';
    display: table;
    clear: both;
}
.Forums__Content {
  margin-left: calc(99.99% * (-1/20 * -1) - (30px - 30px * (-1/20 * -1)) + 30px) !important;
  width: calc(99.99% * 15/20 - (30px - 30px * 15/20));
}
.Forums__Content:nth-child(n){
    float: left;
    margin-right: 30px;
    clear: none;
}
.Forums__Content:last-child{
    margin-right: 0;
}
.Forums__Content:nth-child(20n){
    margin-right: 0;
}
.Forums__Content:nth-child(20n + 1){
    clear: left;
}
.Forums__Sidebar {
  width: calc(99.99% * 4/20 - (30px - 30px * 4/20));
  padding: gutter(1);
}
.Forums__Sidebar:nth-child(n){
    float: left;
    margin-right: 30px;
    clear: none;
}
.Forums__Sidebar:last-child{
    margin-right: 0;
}
.Forums__Sidebar:nth-child(20n){
    margin-right: 0;
}
.Forums__Sidebar:nth-child(20n + 1){
    clear: left;
}
.Forums__Sidebar__CategoriesList {
  margin: 1rem 0 0 0;
  padding: 0;
}
.Forums__Sidebar__CategoriesList__Item {
  display: block;
  text-align: left;
  list-style: none;
  font-size: 1rem;
  padding: 0.2rem 0;
}
.Forums__Sidebar__CategoriesList__Item a {
  color: #000;
  text-decoration: none;
}
.Forums__Sidebar__CategoriesList__Item a:hover {
  color: #f55247;
}
.Forums__Sidebar__CategoriesList__Item__Color {
  display: inline-block;
  border-radius: 5px;
  width: 1rem;
  height: 1rem;
  margin-right: 0.5rem;
}
.Forums__Sidebar__CategoriesList__Item--All:before {
  content: ' ';
  display: inline-block;
  border-radius: 5px;
  width: 1rem;
  height: 1rem;
  margin-right: 0.5rem;
  border: 1px solid #a9a9a9;
}
.Forums__CategoryLabel {
  display: inline-block;
  margin-bottom: 0;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  white-space: nowrap;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  padding: 3px 8px;
  border: 1px solid #dcdcdc;
  border-radius: 4px;
  font-size: 0.8rem;
}
.Forums__TopicList {
  margin: 0;
  padding: 0;
}
.Forums__TopicList__Item {
  list-style: none;
  padding: 0.8rem 1rem;
  *zoom: 1;
}
.Forums__TopicList__Item:before{
    content: '';
    display: table;
}
.Forums__TopicList__Item:after{
    content: '';
    display: table;
    clear: both;
}
.Forums__TopicList__Item:hover {
  background: #eee;
}
.Forums__TopicList__Item__Avatar {
  width: calc(99.99% * 11/100 - (30px - 30px * 11/100));
}
.Forums__TopicList__Item__Avatar:nth-child(n){
    float: left;
    margin-right: 30px;
    clear: none;
}
.Forums__TopicList__Item__Avatar:last-child{
    margin-right: 0;
}
.Forums__TopicList__Item__Avatar:nth-child(100n){
    margin-right: 0;
}
.Forums__TopicList__Item__Avatar:nth-child(100n + 1){
    clear: left;
}
.Forums__TopicList__Item__Content {
  width: calc(99.99% * 80/100 - (30px - 30px * 80/100));
}
.Forums__TopicList__Item__Content:nth-child(n){
    float: left;
    margin-right: 30px;
    clear: none;
}
.Forums__TopicList__Item__Content:last-child{
    margin-right: 0;
}
.Forums__TopicList__Item__Content:nth-child(100n){
    margin-right: 0;
}
.Forums__TopicList__Item__Content:nth-child(100n + 1){
    clear: left;
}
.Forums__TopicList__Item__Excerpt {
  margin-top: 0.5em;
}
.Forums__TopicList__Item__Subject {
  margin: 0 0 0.3em 0;
}
.Forums__TopicList__Item__Metas__Authoring {
  font-size: 0.8rem;
}
.Forums__TopicList__Item__NbReplies {
  width: calc(99.99% * 9/100 - (30px - 30px * 9/100));
}
.Forums__TopicList__Item__NbReplies:nth-child(n){
    float: left;
    margin-right: 30px;
    clear: none;
}
.Forums__TopicList__Item__NbReplies:last-child{
    margin-right: 0;
}
.Forums__TopicList__Item__NbReplies:nth-child(100n){
    margin-right: 0;
}
.Forums__TopicList__Item__NbReplies:nth-child(100n + 1){
    clear: left;
}
.Forums__Searchbar {
  margin-bottom: 1rem;
}
.Forums__Searchbar__Field {
  width: 100%;
  padding: 0.3rem 1rem;
  border-radius: 13px;
  border: 1px solid #d3d3d3;
}
.Button {
  display: inline-block;
  margin-bottom: 0;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  white-space: nowrap;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  line-height: 20px;
  padding: 8px 13px;
  color: #7089a9;
  background: #e7edf3;
  border: 0;
}
.Button_NewTopic {
  width: 100%;
  background-color: #f55247;
  color: #fdfdfd;
  text-decoration: none;
  font-weight: bold;
  font-size: 1.1rem;
}
